/*=====================
        GLOBALS
=====================*/

img {
  max-width: 100%;
}

body {
  background: #fff;
}

/*=====================
        LAYOUT
=====================*/

.wrapper {
  background: #fff;
  min-height: 400px;
}

/* Flexbox */

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.flex {
  -ms-flex: 1 0 200px;
      flex: 1 0 200px;
  margin: 10px;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

/*=====================
    COMPONENT STYLES
=====================*/

.heading {
  text-align: center;
  font-weight: lighter;
  font-size: 1.5em;
}

/* Form */

.form {
  text-align: center;
  margin: 10px;
}

input[type="text"],
textarea,
button {
  width: 100%;
  font-size: 1.25rem;
  margin-bottom: 10px;
  border-radius: 2px;
  outline: none;
}

input[type="text"] {
  height: 55px;
  border: none;
  background-color: #f2f2f2;
}

textarea {
  height: 85px;
  border: none;
  background-color: #f2f2f2;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #90949C;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #90949C;
}

input::placeholder,
textarea::placeholder {
  color: #90949C;
}

button {
  height: 55px;
  border: 2px solid #3FBFAD;
  background-color: transparent;
  color: #3FBFAD;
  border-radius: 2px;
}

button:hover {
  background-color: #3FBFAD;
  color: #fff;
}

.photo-overlay {
  font-size: 1.5rem;
}

/* Transitions & Animations */

.photo {
  position: relative;
  overflow: hidden;
}

.photo img {
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -o-transition: transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}

.photo:hover img {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}

.photo-overlay {
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 10px;
  background: rgba(0,0,0, .75);
}

.photo-overlay {
	opacity: 0;
	-webkit-transition: opacity .5s;
	-o-transition: opacity .5s;
	transition: opacity .5s;
}

.photo-overlay:hover {
	opacity: 1;
}

/* Overlay Text Styles */

.detail-heading {
  font-weight: bolder;
  font-style: italic;
  color: #3FBFAD;
  margin: 0;
}

@media screen and (min-width: 768px) {

  button {
    width: 50%;
  }

  input[type="text"],
  textarea {
    width: 50%;
    font-size: 1rem;
  }

  .flex {
    max-width: 33.33333%;
  }

  .detail-heading {
    font-size: 1.25rem;
  }

  .detail-text {
    font-size: .85rem;
    font-weight: lighter;
  }

}

@media screen and (min-width: 960px) {

  body {
    background: #e9ebee;
  }

  .wrapper {
    max-width: 768px;
    margin: 20px auto;
    border-radius: 2px;
    -webkit-box-shadow: 0px 0px 1px 0px #bfbfbf;
            box-shadow: 0px 0px 1px 0px #bfbfbf;
  }

}

